































import common from '../../common/common.js';
import {
	mapState
} from 'vuex';
export default {
	data() {
		return {
			articlelist: [],
			baseurl: "",
			catid: "",
			title:""
		}
	},
	computed: {
		...mapState(['user']),
		
		
	},
	methods: {
		goback: function() {
			uni.navigateBack({
				delta: 1
			})
		},
		article:function(articleid){
			uni.navigateTo({
				url:"article?articleid="+articleid
			})
		},
		islogin: function() {
			if (this.user.user === null) {
				uni.redirectTo({
					
					url: '../../pages/manage/login'
				})
				return;
			}
		},
		getlist: function() {

			let that = this;
			//console.log(this.user.user.token);
			uni.request({
				url: common.hosturl + "article/articlelist.html",
				method: "POST",
				data: {
					token: this.user.user.token,
					catid: this.catid
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: function(res) {
				//	console.log(JSON.stringify(res.data));
					if (res.data.code == 0) {
						//	console.log(JSON.stringify(res.data.list));
						that.articlelist = res.data.list;
						console.log(res.data.baseurl);
						that.baseurl = res.data.baseurl;


					} else {
						uni.showToast({
							icon: 'none',
							title: '出错啦，请联系客服'
						});
					}
				},
				fail: function() {
					uni.showToast({
						icon: 'none',
						title: '网络错误'
					});
				}
			})
		}
	},
	onLoad: function(e) {
		this.catid = e.catid;
		this.islogin();

	},
	onShow: function(e) {
		this.getlist();
		if(this.catid==7)
		{
			this.title="党建经验";
		}
		else{
			this.title="学习经验";
		}

	}
}



